import React, {useContext} from "react";
import {Fade} from "react-reveal";
import emoji from "react-easy-emoji";
import "./Greeting.scss";
import SocialMedia from "../../components/socialMedia/SocialMedia";
import Button from "../../components/button/Button";
import StyleContext from "../../contexts/StyleContext";
import manontable from "../../assets/images/manOnTable.svg";

export default function Greeting() {
  const {isDark} = useContext(StyleContext);
  return (
    <Fade bottom duration={1000} distance="40px">
      <div className="greet-main" id="greeting">
        <div className="greeting-main">
          <div className="greeting-text-div">
            <div>
              <h1 className={isDark ? "dark-mode greeting-text" : "greeting-text"}>
                {" Hey! I'm Darshil Mehta "}
                <span className="wave-emoji">{emoji("")}</span>
              </h1>
              <p className={isDark ? "dark-mode greeting-text-p" : "greeting-text-p subTitle"}>
                {"An engineer who is driven and willing to learn and use the newest trends in AI and Software Development. I am capable of working with all the layers of the stack to build effective websites that help to solve problems. I'm currently involved in a research project for classification of dermatological infections."}
              </p>
              <SocialMedia />
              <div className="button-greeting-div">
                <Button text="Contact me" href="#contact" />
                <Button
                  text="See my resume"
                  newTab={true}
                  href="https://drive.google.com/file/d/1sAkVgsECeUoVrRRCqGFavZV4jtqwNLsq/view?usp=sharing"
                />
              </div>
            </div>
          </div>
          <div className="greeting-image-div">
              <img
                className="mainPageLogo"
                alt="man sitting on table"
                src={manontable} />
          </div>
        </div>
      </div>
    </Fade>
  );
}
