import React from "react";
import "./Progress.scss";
import {Fade} from "react-reveal";
import skills from "../../assets/images/skill.svg";

export default function StackProgress() {
  const experience = [
    {
      Stack: "Data Science and Analysis", 
      progressPercentage: "82%"
    },
    {
      Stack: "Front end",
      progressPercentage: "88%"
    },
    {
      Stack: "Backend",
      progressPercentage: "83%"
    }
  ]

  const programmingLanguageOne = [
    {
      Stack: "Java", 
      progressPercentage: "84%"
    },
    {
      Stack: "Python",
      progressPercentage: "80%"
    },
    {
      Stack: "C",
      progressPercentage: "74%"
    },
    {
      Stack: "JavaScript", 
      progressPercentage: "80%"
    }
  ]

  const programmingLanguageTwo = [
    {
      Stack: "React.Js",
      progressPercentage: "86%"
    },
    {
      Stack: "Spring Boot",
      progressPercentage: "82%"
    },
    {
      Stack: "Django",
      progressPercentage: "87%"
    },
    {
      Stack: "TensorFlow",
      progressPercentage: "83%"
    },
  ]

  return (
    <div>
      <Fade bottom duration={1000} distance="20px">
        <div className="skills-container">
          <div className="skills-bar">
            <h1 className="skills-heading">Proficiency</h1>
            {experience.map((exp, i) => {
              const progressStyle = {
                width: exp.progressPercentage
              };
              return (
                <div key={i} className="skill">
                  <p>{exp.Stack}</p>
                  <div className="meter">
                    <span style={progressStyle}></span>
                  </div>
                </div>
              );
            })}
          </div>
          <div className="skills-image">
            <img
              alt="Skills"
              src={skills}
            />          
          </div>
        </div>
      </Fade>

      <div className="programmingLanguages">
        <div className="skills-bar-new">
          <h1 className="skills-heading">Programming</h1>
          {programmingLanguageOne.map((exp, i) => {
            const progressStyle = {
              width: exp.progressPercentage
            };
            return (
              <div key={i} className="skill">
                <p>{exp.Stack}</p>
                <div className="meter">
                  <span style={progressStyle}></span>
                </div>
              </div>
            );
          })}
        </div>

        <div className="skills-bar-new">
          <h1 className="skills-heading">Frameworks</h1>
          {programmingLanguageTwo.map((exp, i) => {
            const progressStyle = {
              width: exp.progressPercentage
            };
            return (
              <div key={i} className="skill">
                <p>{exp.Stack}</p>
                <div className="meter">
                  <span style={progressStyle}></span>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </div>

    );
}
